.btnTime {
  margin: 5px;
  width: 80px;
  height: 25px;
  color: black;
  background-color: rgb(140, 198, 255);
  border: 1px solid  rgb(38, 88, 138);
  border-radius: 4px;
  cursor: pointer;
}
.btnControls {
  width: 45%;
  height: 25px;
  margin: 3px;
}
#session-label, #break-label, #session-control  {
  margin: auto;
  width: 100%;
}
#session-label {
  background-color: rgb(192, 192, 192);
  border-bottom: 2px solid rgba(0,0,50,0.25);
}
#break-label {
  background-color: rgb(96, 255, 221);
}
#pomodoro {
  display: flex;
  flex-direction: column;
  margin: auto;
  width: 250px;
  border: 2px outset rgb(215, 224, 233);
  border-radius: 4px;
  box-shadow: 4px 4px 8px 10px  rgba(0,0,30,0.2);
}

/* end of custom styles*/

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
